/*******************
Elements Styles
********************/

/*------------------------------
  Button Styles 
------------------------------*/

.shopbtn {
    color: #2e2e2e;
    font-size: 12px;
    font-weight: 600;
    position: relative;
    text-transform: uppercase;
    transition: all 300ms ease-in 0s;
}

.shopbtn::after {
    background: rgba(0, 0, 0, 0) url("./assets/images/icons/links_static.png") no-repeat scroll 100% 50%;
    content: "";
    display: inline-block;
    height: 11px;
    margin: 0 10px;
    vertical-align: -2px;
    width: 44px;
}

.shopbtn:hover::after {
    background-position: 0 50%;
    transition: all 300ms ease-in 0s;
}

.color__turquoise .shopbtn:hover {
    color: #3cc8df;
}

.color__turquoise .shopbtn:hover::after {
    background-position: 100% 50%;
}

.wnro__btn {
    position: relative;
    z-index: 9 !important;
}

/*-------------------------------
    Section Title Styles
  ---------------------------------*/

.section__title {
    margin: 0 auto;
    max-width: 650px;
}

.section__title.title--white h2 {
    color: #fff;
}

.section__title.title--white h2.title__be--2::after {
    content: url("./assets/images/icons/icon_title3.png");
}

.section__title h2 {
    color: #333;
    display: block;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    margin-bottom: 20px;
    position: relative;
    text-transform: uppercase;
}

.wn__newsletter__area .section__title>h2 {
    letter-spacing: 2px;
}

.section__title--3 h2 {
    color: #2e2e2e;
    display: inline-block;
    font-size: 24px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    vertical-align: top;
}

.section__title--3 h2 {
    color: #2e2e2e;
    font-size: 26px;
    font-weight: 700;
    margin-bottom: 5px;
    text-transform: uppercase;
}

.section__title--3 p {
    color: #333;
    font-size: 12px;
    font-weight: 300;
}

@media only screen and (max-width: 420px) {
    .section__title h2 {
        font-size: 26px;
    }
    .post__itam .content h3 {
        font-size: 18px;
        line-height: 28px;
    }
}

/*------------------------------
