// Needed Files
@import 'variables';
@import 'mixins';



/*******************
Basic Styles
********************/

/**
Reset Styles
*/

* {
  box-sizing: border-box;
}

article, aside, details, figcaption, figure, footer, header, nav, section, summary {
  display: block;
}

audio, canvas, video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden] {
  display: none;
}

html {
  font-size: 100%;
  overflow-y: scroll;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

html, button, input, select, textarea {
  font-family: "Source Sans Pro", Helvetica, sans-serif;
}

body {
  color: #141412;
  line-height: 1.5;
  margin: 0;
}

a {
  color: #333;
  text-decoration: none;
}

a:visited {
  color: #333;
}

a:focus {
  outline: thin dotted;
}

a:active, a:hover {
  color: #e59285;
  outline: 0;
}

a:hover {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6 {
  clear: both;
  font-family: Bitter, Georgia, serif;
  line-height: 1.3;
}

h1 {
  font-size: 48px;
  margin: 33px 0;
}

h2 {
  font-size: 30px;
  margin: 25px 0;
}

h3 {
  font-size: 22px;
  margin: 22px 0;
}

h4 {
  font-size: 20px;
  margin: 25px 0;
}

h5 {
  font-size: 18px;
  margin: 30px 0;
}

h6 {
  font-size: 16px;
  margin: 36px 0;
}

address {
  font-style: italic;
  margin: 0 0 24px;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b, strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

mark {
  background: #e59285;
  color: #000;
}

p {
  margin: 0 0 24px;
}

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 14px;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

pre {
  background: #f5f5f5;
  color: #666;
  font-family: monospace;
  font-size: 14px;
  margin: 20px 0;
  overflow: auto;
  padding: 20px;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

blockquote, q {
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
