}

blockquote {
  font-size: 18px;
  font-style: italic;
  font-weight: 300;
  margin: 24px 40px;
}

blockquote blockquote {
  margin-right: 0;
}

blockquote cite, blockquote small {
  font-size: 14px;
  font-weight: normal;
  text-transform: uppercase;
}

blockquote em, blockquote i {
  font-style: normal;
  font-weight: 300;
}

blockquote strong, blockquote b {
  font-weight: 400;
}

small {
  font-size: smaller;
}

sub, sup {
  font-size: 75%;
